body{
  background-image: linear-gradient(90deg,#444,#999);
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  
}

.slide-container{
  position: relative;
  width: 950px;
  height: 550px;
  margin: 50px auto;
}
.slide {
  width: 100%;
  height: 100%;
}
.slide img{
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-position: center;
  background-size: cover;
  box-shadow: 0 5px 20px 15px rgb(6, 7, 7);
}

.left-arrow,.right-arrow{
  position: absolute;
  width: 40px;
  height: 40px;
  top:45%;
  background-image: url('https://ashrafabusunaina1986.github.io/slide/img/arrow.png');
  background-position: center;
  background-repeat:no-repeat;
  background-size: cover;
  border-radius: 50%;
  z-index: 1;
  box-shadow: 0 0 10px 2px rgb(70, 209, 243);
  transition: opacity 3s;
  opacity: .2;
}
.left-arrow:hover,.right-arrow:hover{
  transition: opacity 3s;
  opacity: 1;
}
.left-arrow{
  left: 20px;
  
}
.right-arrow{
  right: 20px;
  transform: rotate(180deg)
}
.dots-slide{
  display: flex;
  position: absolute;
  top:90%;
  left: 40%;
}
.dot-slide{
  font-size: 40px;
  font-weight: bold;
  margin: 0 5px;
  cursor: pointer;
}
.address-slide{
  position: absolute;
  width: 90%;
  border-radius: 10px;
  top: 0%;
  left: 20px;
  padding: 0 10px;
  font-size: 20px;
  font-weight: bold;
  color: #444;
}
.view-slide{
  margin: 200px auto;
  width: 90%;
  box-shadow: 0 3px 10px 5px #ffff00;
  padding: 20px 10px;
  background-color: #fff;
}
.view-slide img{
  width: 300px;
  height: 200px;
  float: left;
  margin: 10px 50px 15px 0;
  box-shadow: 0 2px 10px 5px #777;
}
 h1,h2,h3,h4,h5,h6{
  color: rgb(15, 15, 14);
  text-shadow: 0px 1px 10px #4d4545;
 }
 .view-slide p{
  text-indent: 20px;
  text-align: left;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Times New Roman', Times, serif,Georgia, Arial, Helvetica, sans-serif ;
  line-height: 20px;
  padding: 5px 20px;
  white-space: break-spaces;
 }
 .clear{
  clear: both;
 }
 .back{
  color: #000;
  border: 1px solid #000;
  background-color: yellow;
  border-radius: 10px;
  padding: 5px 10px;
  text-decoration: none;
  opacity: .5;
 }
 .back:hover{
  opacity: 1;
 }